import {account} from 'api'
import {mapGetters} from 'vuex'
export default {
    data () {
        return {
            account:{}
        }
    },
    computed:{
        ...mapGetters(['userInfo','identityCode','token'])
    },
    methods: {
        getAccount(data){
            account(data).then(res =>{
                if(res.code === 0){
                    this.account = res.result.data
                }
            })
        }
    },
    mounted () {
        let that = this
        that.$nextTick(() =>{
            let data = {
                token:that.token,
                uid: that.userInfo.uid
            }
            that.getAccount(data)
        })
    }
}



